import {useEffect, useState} from 'react';
import {createId} from 'lib/cteateId';
import {useUpdate} from 'hooks/useUpdate';

const useTags = () => {
  const [tags, setTags] = useState<{ id: number, name: string, icon: string }[]>([]);
  useEffect(() => {
    let localTags = JSON.parse(window.localStorage.getItem('tags')|| '[]')
    if(localTags.length === 0 ) {
      localTags = [
        { id: createId(), name: '', icon: 'train'},
        { id: createId(), name: '', icon: 'shop'},
        { id: createId(), name: '', icon: 'shopping'},
        { id: createId(), name: '', icon: 'game'},
        { id: createId(), name: '', icon: 'party'},
        { id: createId(), name: '', icon: 'book'},
        { id: createId(), name: '', icon: 'pet'},
        { id: createId(), name: '', icon: 'babycar'},
        { id: createId(), name: '', icon: 'electric'},
      ]
    }
    setTags(localTags)
  }, []) // 
  useUpdate(() => {
    window.localStorage.setItem('tags', JSON.stringify(tags))
  }, tags)

  const findTag = (id: number) => tags.filter(tag => tag.id === id)[0]
  const findTagIndex = (id: number) => {
      let result = -1
      for(let i=0; i<tags.length; i++) {
        if(tags[i].id === id) {
          result = i;
          break;
        }
      }
      return result;
  }
  const updateTag = (id: number, obj: {name: string}) => {
    setTags(tags.map(tag => tag.id === id ? Object.assign(tag, obj) : tag));
  }
  const deleteTag = (id: number) => {
    setTags(tags.filter(tag => tag.id !== id))
  }
  const addTag = (obj: { name: string, icon: string }) => {
    const {name, icon} = obj
    if(name !== '' && icon !== '') {
      setTags([...tags ,{id: createId(), name, icon}])
    }
  }
  const getName = (id: number) => {
    const tag = tags.filter(t => t.id === id)[0]
    return tag ? tag.name : ''
  }
  return { tags, getName, setTags, findTag, findTagIndex, updateTag, deleteTag, addTag };
}

export {useTags}


